.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto
}

body {
  background-color: #FAFCFF;
  color: #868688;
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif
}

.transparent {
  opacity: 0
}

.opaque {
  opacity: 1
}

header nav {
  min-height: 40px;
  background-color: #ECEEF1;
  border-bottom: 2px solid #868688
}

header nav.navbar-absolute-top {
  position: absolute;
  top: 0;
  left: 0;
  right: 0
}

header nav a {
  color: #868688
}

header nav a:hover {
  color: #606062;
  text-decoration: none
}

header nav ul {
  list-style: none
}

header nav .nav-header {
  position: absolute;
  top: 0;
  left: 0
}

header nav .logo {
  line-height: 40px;
  float: left;
  height: 40px;
  padding: 5px
}

header nav .logo h1 {
  height: 30px;
  width: 50px;
  background-image: url("/img/logo.png");
  background-size: cover;
  text-indent: -99999px;
  margin: 0;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out
}

@media (min-width:650px) {
  header nav .logo h1:hover {
    width: 214px
  }
}

header nav .nav-links {
  text-align: center;
  font-size: 1.25em;
  line-height: 40px
}

@media (max-width:320px) {
  header nav .nav-links {
    text-align: right;
    padding-right: 5px
  }
}

header nav .nav-links ul {
  margin: 0;
  padding: 0
}

header nav .nav-links li {
  display: inline;
  margin-right: 40px
}

header nav .nav-links li:last-child {
  margin-right: 0
}

@media (max-width:360px) {
  header nav .nav-links li {
    margin-right: 10px
  }
}

header nav .nav-other {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 2.5em;
  line-height: 40px;
  padding-right: 5px
}

@media (max-width:320px) {
  header nav .nav-other {
    display: none
  }
}

.affix {
  top: 0;
  left: 0
}

.affixed-sidebar {
  position: relative;
  font-size: 1.4em;
  margin-top: 40px;
  padding: 30px
}

.affixed-sidebar ul li ul li a {
  font-size: 0.8em;
  padding-left: 2em
}

.affixed-sidebar a {
  display: block;
  border-left: 2px solid #FAFCFF
}

.affixed-sidebar .active>a,
.affixed-sidebar a:hover {
  border-left: 2px solid #66A8CC
}

.button {
  border-radius: 4px;
  padding: 10px 30px;
  height: auto
}

@media (max-width:360px) {
  .button {
    width: 100%;
    margin-bottom: 5px
  }
}

.button.gray {
  background-color: #868688;
  border-color: #606062;
  color: #FAFCFF
}

.button.gray:hover {
  background-color: #606062
}

.button.blue {
  background-color: #66A8CC;
  border-color: #3984ac;
  color: #FAFCFF
}

.button.blue:hover {
  background-color: #3984ac
}

.button.white {
  background-color: #FAFCFF;
  border-color: #aeceff;
  color: #868688
}

.button.white:hover {
  background-color: #aeceff
}

section {
  padding: 30px
}

section .title {
  margin-top: 0
}

footer {
  background-color: #868688;
  color: #FAFCFF;
  min-height: 40px;
  height: auto;
  line-height: 20px;
  padding: 10px 20px
}

footer:after {
  content: "";
  display: table;
  clear: both
}

footer a {
  color: #ECEEF1
}

footer a:hover {
  color: #cfd4db
}

footer .copyright {
  float: left;
  width: 50%
}

footer .other-links {
  margin: 0;
  padding: 0;
  float: right
}

@media (max-width:360px) {
  footer .other-links {
    float: none;
    text-align: center
  }
}

footer .other-links li {
  display: inline
}

.hljs {
