}

.main-preview-player {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: justify;
  justify-content: space-between
}

.main-preview-player .video-js,
.main-preview-player .playlist-container {
  position: relative;
  min-width: 300px;
  min-height: 150px;
  height: 0
}

.main-preview-player .video-js {
  -ms-flex: 3 1 70%;
  flex: 3 1 70%
}

.main-preview-player .playlist-container {
  -ms-flex: 1 1 30%;
  flex: 1 1 30%
}

.main-preview-player .playlist-container .vjs-playlist {
  margin: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0
}

.details {
  color: black;
  font-family: sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-line-pack: justify;
  align-content: space-between;
  min-height: 100px;
  overflow: scroll
}

.details .bound-properties,
.details .media-properties,
.details .media-events {
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  -ms-flex-item-align: auto;
  align-self: auto;
  min-width: 200px;
  padding: 10px
}

.details .slider-name,
.details label.checkbox-name,
.details .list-header,
.details .list-item-name {
  font-weight: bold
}

.details .list {
  list-style-type: none;
  padding: 0
}

.details .list .list-header {
  font-size: 1.2em;
  font-variant: small-caps
}

.details .list .list-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 5px
}

.details .list .list-item:nth-child(even) {
  background-color: lightgrey
}

.details .list .list-item-name,
.details .list .list-item-value {
  min-width: 125px
}

.details .list .list-item-name {
  -ms-flex: 1 0 0;
  flex: 1 0
}

.details .list .list-item-value {
  -ms-flex: 1 1 0;
  flex: 1 1;
  overflow-wrap: break-word
}

.details .bound-props-form .slider-value {
  float: right
}

.details .bound-props-form .slider-volume .slider-value:after {
  content: '%'
}



