body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.main-preview-player {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.video-js,
.playlist-container {
  position: relative;
  min-width: 300px;
  min-height: 150px;
  height: 0;
}

.video-js {
  -ms-flex: 3 1 70%;
      flex: 3 1 70%;
}

.playlist-container {
  -ms-flex: 1 1 30%;
      flex: 1 1 30%;
}

.vjs-playlist {
  margin: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}



